#checkout {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    flex-direction: row;

}



.first-last-name,
.email-phone,
.city-state-zip,
.street-address {
    display: flex;

}


.form-boxes {
    width: 65%;
    margin-top: 20px;
}

::-webkit-input-placeholder {
    color: #00000050;
    font-weight: bold;
    font-size: 14px;
}

.first-last-name div,
.email-phone div,
.city-state-zip div,
.street-address div {
    flex: 1;
}

.street-address div:first-of-type {
    flex: 2;
}

#checkout .text-input {
    width: 100%;


}

#zip-code {
    width: 20%
}

.order-summary-box .details {
    display: flex;
    flex-direction: column;
    margin-top: 20px;

}

.order-summary-box .details-flex {
    display: flex;
    justify-content: space-between;
}

.order-summary-box .details-border {
    border-bottom: 1px solid #00000020;
    padding: 20px 0;
}

.order-summary-box button {
    margin-top: 15px;
}

.order-summary-box .details div:last-of-type {
    border: none
}

.order-summary-box input {
    margin-top: 10px;
}

.radio-btn input {
    opacity: 0;
    width: 0;
    position: absolute;
}

.radio-btn {
    display: block;
    position: relative;
    cursor: pointer;
    padding-left: 35px;

}

.radio-btn .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: white;
    transition: .3s ease-in-out;
    border: 1px solid black
}

.order-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;

}

form label {
    text-transform: uppercase;
    width: 100%;
}



#shipping-info select {
    appearance: none;
    padding: 10px;
    background-color: #f5f5f5;
    border: 1px solid #757575;
    transition: .2s ease-in-out;
    border-radius: 3px;
    font-size: 16px;

}

#shipping-info:hover {
    background-color: white;
}

#shipping-info :focus {
    box-shadow: inset 0 -3px 0 #000000;
}

.radio-btn .checkmark::after {
    content: "";
    position: relative;
    display: none;
}

.shipping-options input:checked .radio-btn {
    background-color: black;
}

.radio-btn input:checked~.checkmark {
    background-color: black;
}


.radio-btn input:hover~.checkmark {
    border: 1px solid #000000;
    box-shadow: inset 0 -3px 0 #000000;
}




.radio-btn .checkmark::after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.radio-btn input:checked~.checkmark::after {
    display: block;
}

.shipping-options input {
    padding: 10px;
}

.order-summary-box {
    padding: 10px 20px;
    border: 1px solid #00000020;
    height: auto;
    justify-content: space-between;
    margin-left: 50px;
    width: 40%;

    text-align: left;
}

.email-phone div:first-of-type,
.first-last-name div:first-of-type,
.street-address div:first-of-type,
.city-state-zip div:first-of-type {
    margin-right: 20px;
}

form h2 {
    font-size: 20px;
    color: var(--primary-color);
}

.order-summary-box h2 {
    color: black
}

#checkout section,
.inactive-section,
.completed-section {
    padding: 20px;

    border: .5px solid #00000020;
}

.completed-section a {
    text-align: center;
    display: block;
    margin-top: 20px;
}

.inactive-section {
    background-color: #0000000e;
}

.inactive-section h2,
.completed-section h2 {
    color: #00000030
}

@media only screen and (max-width: 1024px) {

    #checkout {
        display: block;

    }

    .form-boxes {
        width: 100%
    }

    .order-summary-box {
        width: 100%;
        margin: 20px 0;

    }



}