@font-face {
  font-family: Fugaz;
  src: url('assets/fonts/fugazone-regular-webfont.woff') format('woff'),
    url('assets/fonts/fugazone-regular-webfont.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;

}

@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@400;500&display=swap');

@font-face {
  font-family: Fredoka;
  src: url('assets/fonts/Fredoka-VariableFont_wdth_wght.woff') format('woff');
  font-weight: normal;
  font-style: normal;

}

@font-face {
  font-family: Dunbar;
  src: url('assets/fonts/DunbarTall-Regular.woff') format('woff'),
    url('assets/fonts/DunbarTall-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;

}

@font-face {
  font-family: Dunbar;
  src: url('assets/fonts/DunbarText-Bold.woff') format('woff'),
    url('assets/fonts/DunbarText-Bold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;

}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Dunbar, Verdana, sans-serif;
}

*:focus {
  outline: 0;
}


::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: rgb(234, 234, 234);
}

::-webkit-scrollbar-thumb {
  background: #000;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

:root {
  --primary-color: #E20007;
  --accent-color: #83d7f6;

  --bg-color: #fff;
  --second-bg-color: #141617;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 75px;
}

a {
  text-decoration: none;
  cursor: pointer;
  font-family: Dunbar;
}

p,
dd {
  font-family: 'Fredoka', sans-serif;
  font-size: 16px;
  line-height: 1.5em;
}

h1 {
  font-size: 190%;
  line-height: 2em;
}

h2 {
  font-size: 160%;
  line-height: 2em;
}

body {
  background-color: var(--second-bg-color);
  position: relative;

}

.chevron-right {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 22px;
  height: 22px;
  border: 2px solid transparent;
  border-radius: 100px
}

.chevron-right::after {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid;
  border-right: 2px solid;
  transform: rotate(-45deg);
  right: 0px;
  top: 4px
}

.chevron-down {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 22px;
  height: 22px;
  border: 2px solid transparent;
  border-radius: 100px
}

.chevron-down::after {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid;
  border-right: 2px solid;
  transform: rotate(45deg);
  right: 6px;
  top: 4px
}

main {
  background-color: var(--bg-color);
  min-height: 100vh;
  margin-left: 20%;

}

.logo {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

section {
  padding: 10px 2em 3em;

}

dt {
  font-weight: bold;
}

.button-filled {
  background-color: black;
  color: white;
  padding: 12px 20px;
  transition: .1s ease-in-out;
  border-radius: 2px;
  min-width: 100px;
  font-size: 16px;
  border: 1.5px solid black;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.8px;

}

.button-disabled {
  color: white;
  padding: 12px 20px;
  transition: .1s ease-in-out;
  border-radius: 2px;
  min-width: 100px;
  cursor: unset;
  font-size: 16px;
  border: 1.5px solid #00000020;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.8px;
  background-color: #00000020;
}

.button-filled:active,
.button-outlined:active {
  transform: scale(1.02);
}

.button-outlined {
  font-size: 16px;
  min-width: 100px;
  border-radius: 2px;
  background-color: transparent;
  color: black;
  border: 1.5px solid black;
  padding: 12px 20px;
  transition: .2s ease-in-out;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.8px;

}

.button-outlined:hover {
  box-shadow: inset 0 -3px 0 #000000;
}

.button-underlined {
  color: var(--primary-color);
  text-decoration: underline;
  font-size: 14px;
  letter-spacing: 0.8px;
  transition: .2s ease-in-out;
}

.button-filled:hover {
  background-color: #00000080;
  box-shadow: inset 0 -3px 0 #00000080;
  border: 1.5px solid #00000080;

}

.button-underlined:hover {
  opacity: 0.5;
}

label {
  display: block;
  text-align: left;
  font-weight: bold;
  width: 200px;
  padding-right: 1em;
  margin: 20px 0 5px 0;
}

.text-input {
  display: block;
  border-radius: 3px;
  background-color: #f5f5f5;
  transition: .2s ease;
  font-size: 16px;
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #757575;
}

.text-input:hover {
  background-color: white;
}

.text-input:focus {
  border-color: black;
  background-color: white;
  box-shadow: inset 0 -3px 0 #00000090;
}

.wrapper {
  padding: 20px 20px 30px
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 100%;
}

@media only screen and (max-width: 768px) {

  main {
    margin-left: 0;
  }

  .button-filled,
  .button-outlined {
    font-size: 14px;
    padding: 8px 10px;
  }

  html {
    scroll-padding-top: 150px;
  }

  section {
    padding: 1em;
  }

}