ul {
    list-style-type: none;
    margin: 0;
    padding: 0;

}



#categories {
    display: flex;
    justify-content: space-evenly;
}

.deals {
    display: flex;
    overflow-x: auto;
}





.products-list {
    display: flex;
    overflow-x: auto;
}

#mailing-list {
    text-align: center;
    color: black;
    height: 50vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;

}

#mailing-list button {
    width: 30%
}

.shop-all-btn {
    background: url(../../assets/images/mailinglist-graphic.jpeg);
    width: 100%;
    height: 100%;

    background-size: cover;
    transition: .4s ease;
    position: absolute;
    top: 0;
    left: 0;
}

.shop-all-btn:hover {
    transform: scale(1.3);
}

.shop-all-container p {
    color: white;
    font-weight: bold;
    font-size: 26px;
    position: absolute;
    z-index: 2;


}

.shop-all-container p:hover+.shop-all-btn {
    transform: scale(2);
}

.shop-all-container {
    display: flex;
    overflow: hidden;
    min-width: 150px;
    min-height: 150px;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: black;

}

.new-arrival-item {

    height: 150px;
    width: 150px;
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    position: relative;
}

#mailing-list h2 {
    background-image: url(../../assets/images/mailinglist-graphic.jpeg);
    height: 90px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: 40%;
    width: 100%;
    background-repeat: no-repeat;
    font-family: Dunbar;
    font-weight: bold;
    background-color: black;
}

#mailing-list p {

    color: darkgray;
    margin-top: 20px;
    width: 50%
}

#mailing-list button {
    margin-top: 30px;

}


.new-arrivals-scroll {
    display: flex;
    overflow-x: auto;
    align-items: center;
    height: 220px
}

.new-arrival-item img {
    width: 120px;
    height: auto;
}

.new-arrival-item {
    display: flex;
    text-align: left;
    flex-direction: column;
    align-items: center;
    margin: 0 20px;
    width: 200px;

    padding: 10px;
}

.new-arrival-item button {
    color: #00000070;
    text-decoration: underline;
    text-align: center;
    position: absolute;
    bottom: 2px;
}

@media only screen and (max-width: 580px) {
    #categories {
        flex-direction: column;
        align-items: center;
    }

    .shop-all-container {
        min-width: 160px;
        min-height: 160px
    }

    .new-arrival-item {

        min-width: 150px;
        height: 160px;
    }

    .new-arrival-item img {
        width: 100px;
        height: 100px
    }





}